.nav {
  display: flex;
  align-items: center; }
  .nav .nav-bar {
    overflow: hidden;
    line-height: 1.7rem;
    vertical-align: middle; }
    .nav .nav-bar ul {
      margin: 0;
      padding: 0;
      position: relative;
      display: inline-block;
      text-transform: capitalize;
      transition: .8s;
      transform: translate(10%, 0); }
    .nav .nav-bar li {
      position: relative;
      display: inline-block;
      list-style-type: none;
      vertical-align: middle;
      margin-right: 3rem; }
      .nav .nav-bar li a::after {
        content: "";
        position: absolute;
        display: block;
        left: 0;
        bottom: -0.4rem;
        width: 0%;
        border-bottom: solid 2px #EC6C1F;
        transition-duration: 0.5s; }
      .nav .nav-bar li:hover a::after {
        width: 100%; }
      .nav .nav-bar li a {
        position: relative;
        display: inline-block;
        text-decoration: none; }
        .nav .nav-bar li a::after {
          bottom: 0; }
  .nav .nav-icon {
    display: inline-block;
    height: 2.4rem;
    width: 2.4rem;
    margin-left: 1rem; }
    .nav .nav-icon div {
      transition-duration: 1s;
      position: absolute;
      height: 0.2rem;
      width: 2rem;
      background-color: #00416b;
      transform-origin: center;
      top: 50%; }
      .nav .nav-icon div:nth-child(1) {
        transform: translate(0, -0.7rem); }
      .nav .nav-icon div:nth-child(2) {
        transform: translate(0, 0); }
      .nav .nav-icon div:nth-child(3) {
        transform: translate(0, 0.7rem); }
  .nav.open .nav-icon div:nth-child(1) {
    transform: translate(0, 0) rotate(45deg); }
  .nav.open .nav-icon div:nth-child(2) {
    transform: scale(0.0001, 1); }
  .nav.open .nav-icon div:nth-child(3) {
    transform: translate(0, 0) rotate(-45deg); }
  .nav.open .nav-bar ul {
    transform: translate(0, 0); }
  @media (max-width: 768px) {
    .nav .nav-icon {
      position: relative;
      right: 0;
      margin: 0; }
    .nav .nav-bar {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
      .nav .nav-bar ul {
        display: flex;
        flex-direction: column;
        align-items: center; }
      .nav .nav-bar li {
        display: block;
        margin: 0 0 2rem 0; } }

