// @import "../normalize";


.projects {
    position: relative;

    * {
        position: inherit;
    }

    .project-img {
        margin: 0;
        flex: auto 1 1;
        height: auto; 
        width: 100%;   
       
        img {
            width: 100%;
            height: 100%;
        }
    }
    

    .project-content {
        display: flex;
        height: 11%;
        padding: 1rem 0.5rem;
        margin: 0;
        .brand {
            margin: 0;
            margin-top: 2px;
        }
        .line {
            position: relative;
            margin: 3px 5px 0 -1px;
            svg {
                position: relative;
                width: 3rem;
                top: 5px;
                bottom: 0;
            }
        }

        .title {
            text-align: left;
            align-self: flex-end;
            padding: 0px 0 10px 0;
            h3 {
                // font-weight: 500;
            }

            * {
                margin: 0;
            }
        }


    }
}